$image-gap: 32px;

.about-us {
  padding: 30px 0 100px 0;
}

.about-us__title {
  margin: 0 0 14px;
}

.about-us__description {
  margin: 0 0 45px;
}

.employee-container {
  padding-right: $image-gap;
}

.employee {
  display: flex;
  padding: 35px;
  position: relative;
  width: calc(30% - $image-gap);
}

.employee__name {
  margin: 0;

}

.employee__title {
  @include font($size: 1.4rem, $weight: normal);
  margin: 0;

}

.employee__contact {
  @include font($size: 1.4rem, $weight: normal);
  // margin: 0;
  color: $color-link;
  text-decoration: none;
  &:hover {
    text-decoration: underline;
  }

}

.employee__photo {
  width: 98px;
  height: 98px;
  border-radius: 50%;
  position: absolute;
  top: 20px;
  right: -$image-gap;
  box-shadow: 0px 15px 30px rgba(0, 0, 0, 0.15);
}