.promo {
  padding: 30px 0;
  overflow: hidden;
}

.promo__wrapper {
  display: flex;
  justify-content: space-between;
  align-items: center;

}

.promo__content {
  padding-right: 66px;
}

.promo__title {
  margin: 0 0 30px;
}

.promo__buttons>* {
  margin-right: 44px;
}

.promo__text {
  max-width: 487px;
  margin: 0 0 45px;
  @extend %font-add;
  @include font($size: 1.8rem, $height: 2.8rem, $weight: normal);
}