.header {
  box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05);
  background: $color-light;
  padding: 15px 0 17px 0;
}
.header__wrapper {
  display: flex;
  align-items: center;
  justify-content: space-between;
  @include media-tablet {
    padding-left: 24px;
  }
}

.header__logo-line {
  display: flex;
  align-items: center;
  
}

.header__buttons {
  >.button{
    margin-left: 14px;
    &:first-child {
      margin-left: 0;
    }
  }
  display: flex;
  @include media-mobile {
    display: none;
  }

}



.header__buttons>.button:first-child {
  margin-left: 0;
}
