.contact-us {
background: $color-background;
padding: 62px 82px;
  p {
    display: flex;
  }
  p:nth-child(1){
    margin-bottom: 59px;
  }
}

.contact-us__form {
  input, textarea {
    border: 1px solid $color-light;
    background-color: $color-light;
    padding: 10px 21px;
    border-radius: 5px;
    &:focus {
      border-color: $color-primary;
      outline: none;
    }

  }
  textarea {
    resize: none;
    width: 100%;
    height: 160px;
  }
}

.contact-us__line {
  margin-bottom: 19px;
}

.contact-us__button {
  >* {
    min-width: 117px;
  }
}

.contact-us__links {
  text-decoration: none;
  color: $color-text;
  @include font($size: 2.5rem, $height: 2.6rem, $weight: normal);
  margin-left: 11px;
  &:hover {
    text-decoration: underline;
  }

}

.contact-us__title {
  margin-bottom: 3px;
  margin-top: 0px;
}